import { useEffect, useState, memo } from "react";

import { useCountdown } from "hooks/useCountdown";
import { NoteAudioPlayer } from "components/NoteAudioPlayer/NoteAudioPlayer";
import { MusicNote } from "components/MusicNote/MusicNote";
import { NoteCard } from "components/NoteCard/NoteCard";

import { useNoteDetectionHistory } from "hooks/useNoteDetectionHistory";
import { Flashcard, FlashcardType } from "types/Flashcard";

const noteResetDuration = 300;

export const Flashcards = ({ flashcards }: { flashcards: Flashcard[] }) => {
  const [totalPoints, setTotalPoints] = useState(0);
  const [order, setOrder] = useState<{ points: number; card: Flashcard }[]>([]);
  const card = order[0]?.card;
  const [noteDuration, setNoteDuration] = useState(0);
  const { count, reset: resetCountdown, set: setCountdown } = useCountdown(3);

  const { clearHistory } = useNoteDetectionHistory((history) => {
    const sum = history.reduce(
      (partialSum, a) =>
        partialSum + (a.note.name === card.note.name ? a.duration : 0),
      0
    );

    setNoteDuration(sum);

    if (noteDuration >= noteResetDuration) {
      clearHistory();
      resetCard();
    }
  });

  useEffect(() => {
    setOrder(
      flashcards.map((card) => {
        return { card, points: 0 };
      })
    );
  }, [flashcards]);

  const resetCard = () => {
    setNoteDuration(0);
    const newOrder = [...order];
    const item = newOrder.shift();
    if (!item) return;

    if (count > 0) {
      item.points = item.points + 1;
      setTotalPoints(totalPoints + 1);
    } else {
      item.points = 1;
    }

    newOrder.splice(item.points + Math.round(Math.random()), 0, item);

    // if next card is the same as current card,
    // put first new card in front...
    if (newOrder[0].card.note.name === card.note.name) {
      const fromIndex = newOrder.findIndex(
        (o) => o.card.note.name !== card.note.name
      );
      const ele = newOrder[fromIndex];
      newOrder.splice(fromIndex, 1);
      newOrder.unshift(ele);
    }

    setOrder(newOrder);

    resetCountdown();
  };

  if (!card) return <div>loading</div>;

  const showAnswer = count <= 0;

  return (
    <div
      style={{
        minWidth: 300,
      }}
    >
      <div
        style={{
          position: "absolute",
          top: 0,
          left: 0,
          width: `${(100 / noteResetDuration) * noteDuration}vw`,
          height: 16,
          background: "#bada55",
          display: "block",
        }}
      ></div>
      {!showAnswer && (
        <div>
          <div
            style={{
              display: "flex",
              justifyContent: "space-between",
              borderBottom: "1px solid #888",
              paddingBottom: 5,
            }}
          >
            <span>{count}⌛</span>
            <span>{totalPoints}🃏</span>

            <span
              onClick={() => {
                resetCard();
              }}
            >
              {order[0].points}⚡
            </span>
          </div>
          <div
            style={{
              minHeight: 140,
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            {card?.type === FlashcardType.NoteName && <h1>{card.note.name}</h1>}
            {card?.type === FlashcardType.Sound && (
              <NoteAudioPlayer autoplay note={card.note} />
            )}
            {card?.type === FlashcardType.SheetMusicNote && (
              <MemoMusicNote note={card.note} flair={true} />
            )}
          </div>
          <button
            style={{ width: "100%" }}
            onClick={() => {
              setCountdown(0);
            }}
          >
            Reveal
          </button>
        </div>
      )}

      {showAnswer && (
        <div>
          <NoteCard note={card.note} />
          <button
            style={{ width: "100%" }}
            onClick={() => {
              resetCard();
            }}
          >
            Next
          </button>
        </div>
      )}
    </div>
  );
};

const MemoMusicNote = memo(MusicNote);
