import { Flashcards } from "components/Flashcards/Flashcards";

import { useState } from "react";

import { FlashcardType, Flashcard } from "types/Flashcard";
import { beginnerNotes, isNote } from "helpers/notes";
import { note as getNote } from "@tonaljs/core";

export const GamePage = () => {
  const [gameFlashcards, setGameFlashcards] = useState<Flashcard[]>([]);

  const [flashcardOptions, setFlashOptions] = useState({
    cardTypes: Object.values(FlashcardType),
    randomize: false,
  });

  const onCheckChange = (val: FlashcardType) => {
    setFlashOptions({
      ...flashcardOptions,
      cardTypes: flashcardOptions.cardTypes.includes(val)
        ? flashcardOptions.cardTypes.filter((t) => val !== t)
        : [...flashcardOptions.cardTypes, val],
    });
  };

  const startGame = () => {
    let flashcards: Flashcard[] = [];
    beginnerNotes.forEach((n) => {
      const note = getNote(n);
      if (!isNote(note)) return;
      Object.values(FlashcardType).forEach((type) => {
        if (!flashcardOptions.cardTypes.includes(type)) return;
        flashcards.push({
          type,
          note,
          id: Math.random().toString(36).replace("0.", ""),
        });
      });
    });

    if (flashcardOptions.randomize) flashcards.sort(() => 0.5 - Math.random());

    setGameFlashcards(flashcards);
  };

  return (
    <div>
      {gameFlashcards.length === 0 && (
        <>
          <div style={{ display: "flex" }}>
            <div style={{ padding: 8 }}>
              {Object.values(FlashcardType).map((v) => (
                <label key={v} style={{ display: "block", paddingBottom: 12 }}>
                  <input
                    style={{ marginRight: 10 }}
                    type="checkbox"
                    checked={flashcardOptions.cardTypes.includes(v)}
                    onChange={() => {
                      onCheckChange(v);
                    }}
                  />
                  {v}
                </label>
              ))}
            </div>
            <div style={{ padding: 8 }}>
              <label>
                <input
                  style={{ marginRight: 10 }}
                  type="checkbox"
                  checked={!!flashcardOptions.randomize}
                  onChange={() => {
                    setFlashOptions({
                      ...flashcardOptions,
                      randomize: !flashcardOptions.randomize,
                    });
                  }}
                />
                🔀️ Random
              </label>
            </div>
          </div>
          <div style={{ display: "flex", justifyContent: "center" }}>
            <button style={{ marginTop: 20 }} onClick={startGame}>
              Start
            </button>
          </div>
        </>
      )}

      {gameFlashcards.length !== 0 && (
        <Flashcards flashcards={gameFlashcards} />
      )}
    </div>
  );
};
