import { useEffect } from "react";
import useSound from "use-sound";

export const AudioPlayer = ({
  url,
  autoplay = false,
}: {
  url: string;
  autoplay?: boolean;
}) => {
  const [play, { stop }] = useSound(url, { interrupt: true });

  useEffect(() => {
    if (autoplay) play();

    return () => {
      stop();
    };
  }, [stop, play, autoplay]);

  return (
    <div>
      <button
        onClick={() => {
          play();
        }}
      >
        ▶ Play
      </button>
    </div>
  );
};
