import { useState, useEffect } from "react";

import { Sampler } from "tone";

export const useSynth = () => {
  const [sampler, setSampler] = useState<Sampler | null>();
  useEffect(() => {
    const options = {
      onload: () => {
        setSampler(sampler);
      },
    };
    const sampler = new Sampler(
      { A5: "/tin-whistle-trainer/soundfonts/FatBoy/whistle-mp3/A5.mp3" },
      options
    ).toDestination();
  }, [setSampler]);

  return { sampler };
};
