import { Abc } from "components/Abc/Abc";
import { useAppSettings } from "contexts/AppSettingsContext";
import { NoNote, Note } from "@tonaljs/core";

import { scientificToAbcNotation } from "@tonaljs/abc-notation";

export const MusicNote = ({
  note,
  flair = false,
}: {
  note: Note | NoNote;
  flair?: boolean;
}) => {
  const {
    state: { baseNote, showSheetMusicKeySignature },
  } = useAppSettings();

  const diff = (note.oct || 0) - (baseNote.oct || 0);

  const abcNote = scientificToAbcNotation(`${note.letter}${4 + diff}`);

  if (!abcNote) return <></>;

  const flairs = [1, 2, 4, 8];

  const abc = `
    L:1/8
    K: ${showSheetMusicKeySignature ? baseNote.letter : "C"}
    ${abcNote}${!flair ? 2 : flairs[Math.floor(Math.random() * flairs.length)]}
    `;

  return (
    <div
      className={!showSheetMusicKeySignature ? "hide-clef" : ""}
      style={{ width: 120, overflow: "hidden" }}
    >
      <Abc abc={abc} />
    </div>
  );
};

// Example abc notation
// `
// L:1/4
// K:C
// C, D, E, F,|G, A, B, C|D E F G|A B c d|e f g a|b c' d' e'|f' g' a' b'|]
// `
