import { Note } from "@tonaljs/core";
import * as NoteFunctions from "@tonaljs/note";
import { AudioPlayer } from "components/AudioPlayer/AudioPlayer";

export const NoteAudioPlayer = ({
  note,
  autoplay = false,
}: {
  note: Note;
  autoplay?: boolean;
}) => {
  return (
    <AudioPlayer
      url={`/tin-whistle-trainer/soundfonts/FatBoy/whistle-mp3/${NoteFunctions.enharmonic(
        note.name
      )}.mp3`}
      autoplay={autoplay}
    />
  );
};
